  --main-color-1: #333446;
  --main-color-2: #7f8caa;
  --main-color-3: #b8cfce;
  --main-color-4: #eaefef;
}
body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  display: flex;
  flex-direction: row;
  height: 100vh;
  color: var(--main-color-4);
}
button {
  cursor: pointer;
  border: none;
  background-color: var(--main-color-1);
  color: var(--main-color-4);

  &:hover {
    filter: brightness(0.9);
    transform: translate(1px, 1px);
  }
  &:active {
    filter: brightness(1.1);
    transform: translate(-1px, -1px);
  }
}
#sidebar {
  padding: 2rem;
  width: 300px;
  background-color: var(--main-color-1);
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  ul {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin: 0;
    padding: 0;
  }

  .project {
    display: flex;

    .projectButton {
    text-align: left;

    &::before {
      content: ">";
      margin-right: 1rem;
    }
  }
  .projectDeleteButton {
        margin: 0;
        margin-left: auto;
        padding: 0;
        width: 1.5rem;
        height: 1.5rem;
      }
  }

}

#main {
  padding: 2rem;
  background-color: var(--main-color-2);
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  #display {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    .todo {
      display: flex;
      flex-direction: column;

      &[data-status="complete"] {
        text-decoration: line-through;
        opacity: 0.5;
      }
    }
    .todoHeaderDiv {
      cursor: pointer;
      display: flex;
      flex: 1;
      flex-direction: row;
      justify-content: space-between;
      gap: 1rem;

      .todoTitle {
        margin-right: auto;
        cursor: pointer;
      }
      .todoEditButton {
        background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
        background-color: transparent;
        width: 1.5rem;
        height: 1.5rem;
        background-size: contain;
      }
      .todoDeleteButton {
        background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
        background-color: transparent;
        width: 1.5rem;
        height: 1.5rem;
        background-size: contain;
      }
    }
    .todoDescription {
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.2s ease-out;
    }
  }
  #mainBtns {
    margin-top: auto;
  }
}

#newTodoDialog {
  background-color: var(--main-color-2);
  color: var(--main-color-4);
  align-self: center;
  justify-self: center;

  form {
    display: grid;
    grid:
      "title title"
      "description description"
      "dueDate duedate"
      "project priority"
      "submit cancel";
    gap: 1rem;

    #todoFormTitle {
      grid-area: title;
    }
    #todoFormDescription {
      grid-area: description;
    }
    #todoFormDueDate {
      grid-area: dueDate;
    }
    #todoFormPriority {
      grid-area: priority;
    }
    #todoFormProject {
      grid-area: project;
    }
    #todoFormSubmit {
      grid-area: submit;
    }
    #todoFormCancel {
      grid-area: cancel;
    }
  }
}
